@import './mixins.scss';

.config__wrapper {
  display: flex;
  align-items: center;
  margin: 30px 0;
  &:last-child{
    margin: 0;
    margin-top: 30px;
  }
  .config__input {
    flex-basis: 60%;
    border-left: transparent;
  }
  .config__select {
    flex-basis: 40%;
  }
}
.config__add {
  cursor: pointer;
  width: 100%;
  border: none;
  background-color: #000;
  color: #fff;
  border-radius: 15px;
  padding: 5px;
}